html, body {
    width: 100%;
    height: 100%;
    margin: 0;
}

#call-graph-plot {
    width: 100%;
    height: 100%;
}

#call-graph-plot svg {
    width: 100%;
    height: 100%;
}

text {
    font-weight: 300;
    font-family: "Helvetica Neue", Helvetica, Arial, sans-serf, serif;
    font-size: 14px;
}

.node rect {
    stroke: #333;
    fill: #fff;
    stroke-width: 1px;
}

.edgePath path.path {
    stroke: #333;
    fill: none;
    stroke-width: 1px;
}

.arrowhead {
   stroke: #333;
   fill: #333;
   stroke-width: 1px;
}

svg text {
    cursor: default;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
}
